'use client'

import { useState } from 'react'
import { urlFor } from '@/sanity/lib/image'
import Image from 'next/image'

type TabSectionProps = {
    heading?: string
    tabs: {
        _key: string
        title: string
        image: any
    }[]
}

export function TabSection({ heading, tabs }: TabSectionProps) {
    const [activeIndex, setActiveIndex] = useState(0)

    return (
        <section className="py-12 w-[1600] mx-auto">
            {heading && <h2 className="text-3xl font-bold text-center mb-8">{heading}</h2>}
            <div className="max-w-4xl mx-auto">
               

                {tabs[activeIndex]?.image && (
                    <div className="flex justify-center">
                        <Image
                            src={urlFor(tabs[activeIndex].image).width(600).height(400).url()}
                            alt={tabs[activeIndex].title}
                            width={970}
                            height={430}
                            className="rounded-lg shadow-lg object-contain"
                        />
                    </div>
                )}
                <div className="flex justify-center gap-x-28 mb-6  border-gray-300 py-20">
                    {tabs.map((tab, index) => (
                        <button
                            key={tab._key}
                            onClick={() => setActiveIndex(index)}
                            className={` py-4 px-4 font-medium border-b-2 ${activeIndex === index ? ' border-orange-500' : 'text-black-500 border-gray-300'
                                }`}
                        >
                            {tab.title}
                        </button>
                    ))}
                </div>
            </div>
        </section>
    )
}
