import { PAGE_QUERYResult } from "@/sanity/types";
import Image from "next/image";
import { urlFor } from "@/sanity/lib/image";

type FeaturesProps = Extract<
    NonNullable<NonNullable<PAGE_QUERYResult>["content"]>[number],
    { _type: "cardSection" }
>;

export function CardSection({ cards, title }: FeaturesProps) {
    return (
        <section className="py-12 w-[1200] mx-auto">
            {title && (
                <h2 className="text-3xl font-bold text-center mb-12">{title}</h2>
            )}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-10">
                {cards?.map((card) => (
                    <div
                        key={card._key}
                        className={`relative overflow-hidden rounded-lg p-8 bg-white`}
                    >
                        {/* Background Image */}
                        {card.bgimage && (
                            <div className="absolute inset-0">
                                <Image
                                    src={urlFor(card.bgimage).width(800).height(600).url()}
                                    alt="Background"
                                    fill
                                    style={{ objectFit: 'cover' }}
                                />
                            </div>
                        )}

                        {/* Content */}
                        <div className="relative z-10 flex flex-col gap-6 items-center">
                            {/* Icon */}
                            {card.icon && (
                                <div className=" flex items-center justify-center">
                                    <Image
                                        src={urlFor(card.icon).width(270).height(270).url()}
                                        alt={card.title}
                                        width={270}
                                        height={270}
                                        className="object-contain"
                                    />
                                </div>
                            )}
                            <div className={`text-center ${card.textColor || 'text-white'}`}>
                                {/* Title */}
                                {card.title && (
                                    <h3 className="text-2xl font-semibol">
                                        {card.title}
                                    </h3>
                                )}

                                {/* Text */}
                                {card.text && (
                                    <p className="text-lg">
                                        {card.text}
                                    </p>
                                )}
                            </div>
                            {/* Button */}
                            {card.btntext && card.btnurl && (
                                <a
                                    href={card.btnurl}
                                    className="mt-4 inline-block bg-orange-600 text-white px-5 py-3 rounded hover:bg-blue-700 transition"
                                >
                                    {card.btntext}
                                </a>
                            )}
                        </div>
                    </div>
                ))}
            </div>
        </section>
    );
}