import Image from 'next/image'
import { urlFor } from '@/sanity/lib/image'

type ImageTextSectionProps = {
    title?: string
    description?: string
    mainImage?: never
    backgroundImage?: never
    imagePosition?: 'left' | 'right'
}

export function ImageTextSection({
    title,
    description,
    mainImage,
    backgroundImage,
    imagePosition = 'right',
}: ImageTextSectionProps) {
    const isImageLeft = imagePosition === 'left'

    return (
        <section
            className="relative bg-cover bg-center p-14 bg-white w-7xl mx-auto my-18"
            style={{
                backgroundImage: backgroundImage ? `url(${urlFor(backgroundImage).url()})` : 'none',
            }}
        >
            <div className="max-w-6xl mx-auto grid grid-cols-1 md:grid-cols-2 items-center gap-12">
                {isImageLeft && mainImage && (
                    <div className="flex justify-center">
                        <Image
                            src={urlFor(mainImage).width(500).url()}
                            alt="Main"
                            width={500}
                            height={400}
                            className="rounded-xl shadow-xl object-contain"
                        />
                    </div>
                )}

                <div className="text-slate-800 space-y-4">
                    {title && <h2 className="text-3xl font-bold">{title}</h2>}
                    {description && <p className="text-lg text-slate-600">{description}</p>}
                </div>

                {!isImageLeft && mainImage && (
                    <div className="flex justify-center">
                        <Image
                            src={urlFor(mainImage).width(100).url()}
                            alt="Main"
                            width={100}
                            height={100}
                            className="rounded-xl shadow-xl object-contain"
                        />
                    </div>
                )}
            </div>
        </section>
    )
}
